.CreateFoodContainer {
  max-width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
 
  
  
}

.CreateFoodBox {
  background-color: white;
  width: 45vw;

  box-sizing: border-box;
  border: 2px solid tomato;
  overflow: hidden;
}

.CreateFoodBox > div > button {
  background-color: tomato;
  height: 3px;
  width: 50%;
  border: none;
  transition: all 0.5s;
}

.CreateFoodForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
  padding: 2vmax;
  justify-content: space-evenly;
  height: 70%;
  transition: all 0.5s;
}

.CreateFoodForm > div {
  display: flex;
  width: 100%;
  align-items: center;
}

.CreateFoodForm > div > select {
  padding: 1vmax 4vmax;
  padding-right: 1vmax;
  margin: 1rem 0;
  width: 100%;
  box-sizing: border-box;
  border: 1px solid rgba(0, 0, 0, 0.267);
  border-radius: 4px;
  font: 300 0.9vmax cursive;
  outline: none;
}
.CreateFoodForm > div > select:focus {
  border: 1px solid tomato;
}
.CreateFoodForm > div > input {
  padding: 2vmax 4vmax;
  padding-right: 1vmax;
  width: 100%;
  box-sizing: border-box;
  border: 1px solid rgba(0, 0, 0, 0.267);
  border-radius: 4px;
  font: 300 0.9vmax cursive;
  outline: none;
}

.CreateFoodForm > div > input:focus {
  border-bottom: 1px solid tomato;
}
.CreateFoodForm > div > svg {
  position: absolute;
  transform: translateX(1vmax);
  font-size: 1.6vmax;
  color: rgba(0, 0, 0, 0.623);
}

.CreateFood > a {
  color: rgba(0, 0, 0, 0.651);
  text-decoration: none;
  align-self: flex-end;
  transition: all 0.5s;
  font: 500 0.8vmax "Gill Sans";
}

.CreateFoodForm > a:hover {
  color: black;
}

#foodImage > img {
  width: 3vmax;
  border-radius: 100%;
}
#foodImage > input {
  display: flex;
  padding: 0%;
}

#foodImage > input::file-selector-button {
  cursor: pointer;
  width: 100%;
  z-index: 2;
  height: 5vh;
  border: none;
  margin: 0%;
  font: 400 0.8vmax cursive;
  transition: all 0.5s;
  padding: 0 1vmax;
  color: rgba(0, 0, 0, 0.623);
  background-color: rgb(255, 255, 255);
}

#foodImage > input::file-selector-button:hover {
  background-color: rgb(235, 235, 235);
}

.addFoodBtn {
  border: none;
  font-weight: bold;
  background-color: #e65100;
  color: white;
  font: 300 0.9vmax "Roboto";
  width: 100%;
  padding: 0.8vmax;
  cursor: pointer;
  transition: all 0.5s;
  border-radius: 4px;
  outline: none;
  margin-top: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.219);
}

.addFoodBtn:hover {
  background-color: rgb(179, 66, 46);
}

.CreateFoodForm > div > input {
  width: 100%;
  height: 5vh;
  border: none;
  border-bottom: 1px solid rgb(231, 231, 231);
  outline: none;
  transition: all 0.5s;
}

@media screen and (max-width: 600px) {
  .CreateFoodContainer {
    background-color: white;
  }
  .CreateFoodBox {
    width: 90vw;
    height: 85vh;
  }
  .CreateFoodForm {
    height: 100%;
    padding: 5vmax;
  }

  .CreateFoodForm > div > input,
  .CreateFoodForm > div > select {
    padding: 2.5vmax 5vmax;
    font: 300 1.7vmax cursive;
  }

  .CreateFoodForm > div > svg {
    font-size: 2.8vmax;
  }

  .CreateFoodForm > a {
    font: 500 1.8vmax "Gill Sans";
  }

  #foodImage > img {
    width: 8vmax;
    border-radius: 100%;
  }

  #foodImage > input::file-selector-button {
    height: 7vh;
    font: 400 1.8vmax cursive;
  }

  .addFoodBtn {
    font: 300 1.9vmax "Roboto";
    padding: 1.8vmax;
  }
}
